<template>
  <!-- 
  
  init-show
  kehuxinxi
  shangjiguanli
 -->
  <div class="KeHu">
    <init-show @func="getMsgKHKF"></init-show>
    <kehuxinxi v-if="flag1"></kehuxinxi>
    <shangjiguanli v-if="flag2"></shangjiguanli>
  </div>
</template>

<script>
import initShow from "@/components/initShow.vue";
import shangjiguanli from "@/components/shangjiguanli.vue";
import kehuxinxi from "@/components/kehuxinxi.vue";
export default {
  //
  data() {
    return {
      flag1: true,
      flag2: false,
    };
  },
  //
  components: {
    initShow,
    shangjiguanli,
    kehuxinxi,
  },
  //
  mounted() {},
  //
  methods: {
    getMsgKHKF(data) {
      if (data == 2) {
        this.flag2 = true;
        this.flag1 = false;
      } else {
        this.flag2 = false;
        this.flag1 = true;
      }
    },
  },
};
</script>
